                                                            "value": "Швейцарский стиль (Swiss). Дизайн из этой страны давно стал легендарным. Ни \nодин графдизайнер не имеет права упустить изучение этого стиля. Вы поймёте, что пространство макета можно проектировать. Относиться к нему как инженер. Вы увидите швейцарский принцип Lagom в действии — не много и не мало, ровно в самый раз. Нейтральность и сдержанность обогатят вашу эстетику.",
