                                                            "value": "Клубная культура из Германии. Вычищенный электронный звук. Механические гипнотические ритмы. Вокруг этой музыки собралась большая культура, в том числе визуальная. И мы изучим её. Поймём, как графдизайнят в интеллектуальном андерграунде. Туц-туц-туц-сигма.\r",
